@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.container{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.mainButton{
  background-color: #e28c5f;
  /*display: inline-block;*/
  text-align: center;
  min-width: 40vmin;
  max-width: 60vmin;
  min-height: 10vmin;

  color: black;
  font-size: 2vmax;
  padding: 2rem;
  outline: none;
  border: 0;
  border-radius: 1vmin;
}
.mainButton:hover{
  background-color: #e26d45;
}
.dimmerContainer{
  /*z-index: -1;*/
  /*background-color: rgba(128, 57, 105, 0.23);*/
  min-width: 100vw;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.dimmerWrapper{
  position: absolute;
  background-color: rgb(251, 248, 201);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10vmin;
  width: 70vmin;
  height: 20vmin;
  font-size: 1.7vmax;
  border-radius: 2vmin;

}
